body {
  box-sizing: border-box;
  color: #fff;
  background-image: url("2k_stars_milky_way.724715d3.jpg");
  margin: 0;
  padding: 0;
}

canvas {
  display: block;
}

.container {
  background-color: #1c1f2b;
  border-radius: 10px;
  width: 1300px;
  height: 80px;
  margin-top: 30px;
  margin-left: 60px;
  font-size: large;
  position: relative;
}

.container:after, .container:before {
  --angle: 0deg;
  content: "";
  background-image: conic-gradient(from var(--angle), transparent 50%, #ff4545, #0f9, #006aff, #ff0095, #ff4545);
  z-index: -1;
  border-radius: 10px;
  width: 100%;
  height: 100%;
  padding: 3px;
  animation: 5s linear infinite spin;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.container:before {
  filter: blur(1.5rem);
  opacity: .5;
}

nav a {
  float: right;
  color: #f0f8ff;
  margin-right: 10px;
  padding-left: 20px;
  text-decoration: none;
  transform: translateX(-20px);
}

nav a:hover {
  cursor: pointer;
  color: #8a2be2;
}

.logo {
  transform: translate(20px, 18px);
}

@keyframes spin {
  from {
    --angle: 0deg;
  }

  to {
    --angle: 360deg;
  }
}

@property --angle {
  syntax: "<angle>";
  inherits: false;
  initial-value: 0deg;
}

.menu {
  text-align: center;
  justify-content: space-between;
  width: 35%;
  margin-left: 35%;
  display: flex;
}

.arrow-l, .arrow-r {
  background-color: #1c1f2b;
