body {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    background-image: url(textures/2k_stars_milky_way.jpg);
    color: #ffffff;
}
canvas {
    display: block;
}
.container {
    border-radius: 10px;
    font-size: large;
    margin-top: 30px;
    background-color: #1c1f2b ;
    margin-left: 60px;
    width: 1300px;
    height: 80px;
    position: relative;
}

.container::after, .container::before {
    --angle: 0deg;
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    top: 50%;
    transform: translateY(-50%);
    background-image: conic-gradient(from var(--angle), transparent 50%, #ff4545, #00ff99, #006aff, #ff0095, #ff4545);
    z-index: -1;
    border-radius: 10px;
    padding: 3px;
    animation: 5s spin linear infinite;
}
.container::before {
    filter:blur(1.5rem);
    opacity: 0.5; 
}
nav a {
    transform: translateX(-20px);
    margin-right: 10px;
    padding-left: 20px;
    float: right; 
    text-decoration: none;
    color: aliceblue;
}
nav a:hover {
    cursor: pointer;
    color:blueviolet;
}
.logo {
    transform: translate(20px, 18px);
}

@keyframes spin {
    from{
        --angle: 0deg;
    }
    to{
        --angle: 360deg;
    }
}
@property --angle {
    syntax: "<angle>";
    initial-value: 0deg;
    inherits: false;
}


.menu {
    width: 35%;
    display: flex;
    justify-content: space-between;
    margin-left: 35%;
    text-align: center;
}

.arrow-l, .arrow-r {
    width: 100px;
    height: 50px;
    border-radius: 10px;
    background-color: #1c1f2b;
    border: 1px solid #1c1f2b;
}

.arrow-l:hover, .arrow-r:hover {
    background-color: transparent;
    cursor: pointer;
}

.arrow-l:active, .arrow-r:active {
    background-color: #257454;
}

.nav2 {
    display: flex;
    justify-content: center;
    text-align: center;
}

ul {
    gap: 20px;
    display: flex;
}

ul li {
    list-style: none;
}

ul li a {
    text-decoration: none;
    color: white;
}

ul li a:hover {
    color: blueviolet;
}

.t101 {
    border: 1px solid orange;
    margin-left: 30%;
    width: 45%;
    color: #1c1f2b;
    background-color: #f1f1c1;
}
td {
    border: 1px solid orange;
}

h3 {
    margin-left: 30%;
}

.it {
    padding-left: 240px;
    transform: translateY(-30px);
}

a {
    color: white;
    text-decoration: none;
}

a:hover {
    color:#257454;
}


form {
    padding-left: 35%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    width: 30%;
}

fieldset {
    padding: 20px;
}

#dateInput {
    background-color: #1c1f2b;
    color: #ffffff;
    padding: 10px;
    border-radius: 13%;
    border: 2px solid #1c1f2b;
}

#dateInput:hover {
    background-color: transparent;
    cursor: pointer;
    color:#00ff99;
}

#dateInput:active {
    background-color: #00ff99;
    color: #1c1f2b;
}

h2 {
    text-align: center;
}

#tableHeader {
    gap: 30%;
    margin-left: 25%;
}
